import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CityComponent } from './components/city/city.component';
import { DashboardComponent } from './components/dashboard.component';
import { RouteComponent } from './components/route/route.component';
import { SightComponent } from './components/sight/sight.component';
import { CityInfoComponent } from './components/city-info/city-info.component';
import { RouteInfoComponent } from './components/route-info/route-info.component';
import { SightInfoComponent } from './components/sight-info/sight-info.component';
import { CitiesDataResolver, CityInfoResolver, RoutesDataResolver, RouteInfoResolver, SightsDataResolver, SightInfoResolver, AudiosDataResolver, StylesDataResolver, AccesscodInfoResolver, AccesscodeDataResolver } from './resolvers'
import { AccesscodesComponent } from './components/accesscodes/accesscodes.component';
import { AccesscodesInfoComponent } from './components/accesscodes-info/accesscodes-info.component';

export const dashboardRoutes: Routes = [
  {
    path: '', component: DashboardComponent, resolve: { CitiesDataResolver, RoutesDataResolver, SightsDataResolver, AudiosDataResolver, StylesDataResolver, AccesscodeDataResolver }, children: [
      { path: '', redirectTo: 'city', pathMatch: 'full' },
      { path: 'city', component: CityComponent },
      { path: 'route', component: RouteComponent },
      { path: 'sight', component: SightComponent },
      { path: 'accesscodes', component: AccesscodesComponent },
      { path: 'city-info/:id', component: CityInfoComponent, resolve: { CityInfoResolver } },
      { path: 'route-info/:id', component: RouteInfoComponent, resolve: { RouteInfoResolver } },
      { path: 'sight-info/:id', component: SightInfoComponent, resolve: { SightInfoResolver } },
      { path: 'accesscodes-info/:id', component: AccesscodesInfoComponent, resolve: { AccesscodInfoResolver } },
      { path: 'addcity', component: CityInfoComponent },
      { path: 'addroute', component: RouteInfoComponent },
      { path: 'addsight', component: SightInfoComponent },
      { path: 'addaccesscodes', component: AccesscodesInfoComponent },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(dashboardRoutes)],
  exports: [RouterModule],
  providers: []
})
export class DashboardRoutingModule { }
