import { Action } from '@ngrx/store';
import { RouteInterface } from 'src/modules/dashboard/interfaces';
import { UpdateRouteActionInterface } from '../../interfaces/update-route-actions.interface';

export namespace RouteActions {

  export enum Actions {
    LoadRoutesData = '[Route] LoadRoutesData',
    SetRoutesData = '[Route] SetRoutesData',
    SetActiveRouteData = '[Route] SetActiveRouteData',
    DeleteRouteData = '[Route] DeleteRouteData',
    AddRouteData = '[Route] AddRouteData',
    LoadActiveRouteData = '[Route] LoadActiveRouteData',
    UpdateActiveRouteData = '[Route] UpdateActiveRouteData'
  }

  export class LoadRoutesDataAction implements Action {
    readonly type: Actions = Actions.LoadRoutesData;
    constructor() { }
  }

  export class LoadActiveRouteDataAction implements Action {
    readonly type: Actions = Actions.LoadActiveRouteData;
    constructor(public payload: string) { }
  }

  export class SetRoutesDataAction implements Action {
    readonly type: Actions = Actions.SetRoutesData;
    constructor(public payload: RouteInterface[]) { }
  }

  export class SetActiveRouteDataAction implements Action {
    readonly type: Actions = Actions.SetActiveRouteData;
    constructor(public payload: RouteInterface) { }
  }

  export class UpdateActiveRouteDataAction implements Action {
    readonly type: Actions = Actions.UpdateActiveRouteData;
    constructor(public payload: UpdateRouteActionInterface) { }
  }

  export class DeleteRouteDataAction implements Action {
    readonly type: Actions = Actions.DeleteRouteData;
    constructor(public payload: string) { }
  }

  export class AddRouteDataAction implements Action {
    readonly type: Actions = Actions.AddRouteData;
    constructor(public payload: RouteInterface) { }
  }

  export type RoutesActionsType = LoadRoutesDataAction |
                                  LoadActiveRouteDataAction |
                                  SetRoutesDataAction |
                                 SetActiveRouteDataAction |
                                 UpdateActiveRouteDataAction |
                                 DeleteRouteDataAction |
                                 AddRouteDataAction;
}
