import { AngularFirestoreCollection, AngularFirestore, AngularFirestoreDocument, DocumentReference } from 'angularfire2/firestore';
import { AudioInterface, StyleInterface } from '../interfaces';
import { map } from 'rxjs/operators';
import { Observable, from } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable()
export class StylesApiService {

  private stylesCollection: AngularFirestoreCollection<StyleInterface> = this.afs.collection<StyleInterface>('styles');

  constructor(private afs: AngularFirestore) { }

  getStyles(): Observable<StyleInterface[]> {
    return this.stylesCollection.snapshotChanges().pipe(
      map(actions => actions.map(a => {
        const data = a.payload.doc.data() as StyleInterface;
        const id = a.payload.doc.id;
        return { id, ...data };
      }))
    )
  }
}
