import { Injectable } from "@angular/core";
import { Resolve, ActivatedRouteSnapshot } from '@angular/router';
import { Store } from '@ngrx/store';
import { TemplarState } from '../state-management';
import { Observable } from 'rxjs';
import { CitiesActions } from '../state-management/states/cities';
import { ofType, Actions } from '@ngrx/effects';
import { take } from 'rxjs/operators';

@Injectable()
export class CityInfoResolver implements Resolve<void> {
  constructor(private store: Store<TemplarState>, private actions: Actions) { }

  resolve(route: ActivatedRouteSnapshot): Observable<void> {
    this.store.dispatch(new CitiesActions.LoadActiveCityDataAction(route.params.id));
    return this.actions.pipe(ofType(CitiesActions.Actions.SetActiveCityData), take(1));
  }
}