import { Component } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { takeUntil } from 'rxjs/operators';
import { CityInterface, SightInterface } from '../../interfaces';
import { Store } from '@ngrx/store';
import { TemplarState, sightSelectors } from '../../state-management';
import { AbstractComponent } from 'src/modules/shared';
import { SightActions } from '../../state-management/states/sight';
import { Router } from '@angular/router';

@Component({
  selector: 'app-sight',
  templateUrl: './sight.component.html',
  styleUrls: ['./sight.component.scss']
})
export class SightComponent extends AbstractComponent {

  sights$: Observable<SightInterface[]> = this.store.select(sightSelectors.getSightsSelector).pipe(takeUntil(this.destroyed$));

  constructor(private router: Router, private store: Store<TemplarState>) {
    super();
  }

  onAddItem(): void {
    this.router.navigate(['addsight']);
  }

  onEditItem(item: CityInterface): void {
    this.router.navigate([`sight-info/${item.id}`]);
  }

  onDeleteItem(item: CityInterface): void {
    confirm(`Delete "${item.name._en}"?`) ? this.store.dispatch(new SightActions.DeleteSightDataAction(item.id)) : null;
  }
}
