import { TemplarState, CityStateInterface, RouteStateInterface, SightStateInterface, AudiosStateInterface, StylesStateInterface, AccesscodesStateInterface } from './interfaces';

import * as fromCities from './states/cities/cities.reducer';
import * as fromRoutes from './states/route/route.reducer';
import * as fromSight from './states/sight/sight.reducer';
import * as fromAudios from './states/audios/audios.reducer';
import * as fromStyles from './states/styles/styles.reducer';
import * as fromAccesscodes from './states/accesscodes/accesscodes.reducer';

import { createSelector } from '@ngrx/store';
import { CityInterface, RouteInterface, SightInterface, AudioInterface, StyleInterface, AccesscodesInterface } from '../interfaces';


export const templarReducers = {
  templar_cities: fromCities.citiesReducer,
  templar_routes: fromRoutes.routeReducer,
  templar_sights: fromSight.sightReducer,
  templar_audios: fromAudios.audiosReducer,
  templar_styles: fromStyles.stylesReducer,
  templat_accesscodes: fromAccesscodes.accesscodesReducer,
};

export const getCitiesState: (state: TemplarState) => CityStateInterface = (state: TemplarState) => state ? state.templar_cities : null;
export const getRoutesState: (state: TemplarState) => RouteStateInterface = (state: TemplarState) => state ? state.templar_routes : null;
export const getSightState: (state: TemplarState) => SightStateInterface = (state: TemplarState) => state ? state.templar_sights : null;
export const getAudiosState: (state: TemplarState) => AudiosStateInterface = (state: TemplarState) => state ? state.templar_audios : null;
export const getStyleState: (state: TemplarState) => StylesStateInterface = (state: TemplarState) => state ? state.templar_styles : null;
export const getAccesscodesState: (state: TemplarState) => AccesscodesStateInterface = (state: TemplarState) => state ? state.templat_accesscodes : null;

// ***** Cities selectors *****

export const getCitiesData: (state: CityStateInterface) => CityInterface[] = (state: CityStateInterface) => state ? state.cities : undefined;
export const getActiveCityData: (state: CityStateInterface) => CityInterface = (state: CityStateInterface) => state ? state.activeCity : undefined;

export const citiesSelectors = {
  getCitiesSelector: createSelector(getCitiesState, getCitiesData),
  getActiveCitySelector: createSelector(getCitiesState, getActiveCityData)
}

// ***** Routes selectors *****

export const getRoutesData: (state: RouteStateInterface) => RouteInterface[] = (state: RouteStateInterface) => state ? state.routes : undefined;
export const getActiveRouteData: (state: RouteStateInterface) => RouteInterface = (state: RouteStateInterface) => state ? state.activeRoute : undefined;

export const routesSelectors = {
  getRoutesSelector: createSelector(getRoutesState, getRoutesData),
  getActiveRouteSelector: createSelector(getRoutesState, getActiveRouteData)
}

// ***** Sight selectors *****

export const getSightsData: (state: SightStateInterface) => SightInterface[] = (state: SightStateInterface) => state ? state.sights : undefined;
export const getActiveSightData: (state: SightStateInterface) => SightInterface = (state: SightStateInterface) => state ? state.activeSight : undefined;

export const sightSelectors = {
  getSightsSelector: createSelector(getSightState, getSightsData),
  getActiveSightSelector: createSelector(getSightState, getActiveSightData)
}

// ***** Audios selectors *****

export const getAudiosData: (state: AudiosStateInterface) => AudioInterface[] = (state: AudiosStateInterface) => state ? state.audios : undefined;

export const audioSelectors = {
  getAudiosSelector: createSelector(getAudiosState, getAudiosData)
}

// ***** Styles selectors *****

export const getStylesData: (state: StylesStateInterface) => StyleInterface[] = (state: StylesStateInterface) => state ? state.styles : undefined;

export const styleSelectors = {
  getStylesSelector: createSelector(getStyleState, getStylesData)
}

// ***** Accesscodes selectors *****

export const getAccesscodesData: (state: AccesscodesStateInterface) => AccesscodesInterface[] = (state: AccesscodesStateInterface) => state ? state.accesscodes : undefined;
export const getActiveAccesscodData: (state: AccesscodesStateInterface) => AccesscodesInterface = (state: AccesscodesStateInterface) => state ? state.activeAccesscod : undefined;

export const accesscodesSelectors = {
  getAccesscodesSelector: createSelector(getAccesscodesState, getAccesscodesData),
  getActiveAccesscodSelector: createSelector(getAccesscodesState, getActiveAccesscodData)
}
