import { AccesscodesStateInterface } from '../../interfaces';
import { AccesscodesActions } from './accesscodes.actions';
import { AccesscodesInterface } from 'src/modules/dashboard/interfaces';


const initialState: AccesscodesStateInterface = {
  accesscodes: [],
  activeAccesscod: null
};

export function accesscodesReducer(state: AccesscodesStateInterface = initialState, action: AccesscodesActions.AccesscodesActionsType): AccesscodesStateInterface {
  switch (action.type) {
    case AccesscodesActions.Actions.SetAccesscodesData: {
      return { ...state, accesscodes: <AccesscodesInterface[]>action["payload"] };
    }
    case AccesscodesActions.Actions.SetActiveAccesscodData: {
      return { ...state, activeAccesscod: <AccesscodesInterface>action["payload"] };
    }
    default: {
      return state;
    }
  }
}