import { Injectable } from '@angular/core';
import { Effect, Actions, ofType } from '@ngrx/effects';
import { Action, Store } from '@ngrx/store';
import { combineLatest, identity, Subscription } from 'rxjs';
import { switchMap, mergeMap } from 'rxjs/operators';
import { CitiesActions } from './cities.actions';
import { CityApiService } from 'src/modules/dashboard/services';
import { CityInterface } from 'src/modules/dashboard/interfaces';
import { TemplarState } from '../../interfaces';
import { UpdateCityActionInterface } from '../../interfaces/update-city-actions.interface';

@Injectable()
export class CitiesEffects {
  getCitySubscription: Subscription;
  constructor(private actions: Actions,
    private CityapiService: CityApiService,
    private store: Store<TemplarState>
  ) { }

  @Effect()
  loadCitiessData$: any = this.actions.pipe(ofType(CitiesActions.Actions.LoadCitiesData))
    .pipe(switchMap((action: Action) => {
      this.CityapiService.getCities().subscribe((cities: CityInterface[]) => {
        cities.sort((a, b) => {
          return a.position < b.position ? -1 : 1;
        })
        this.store.dispatch(new CitiesActions.SetCitiesDataAction(cities));
      });
      return [];
    }));

  @Effect()
  deleteCityData$: any = this.actions.pipe(ofType(CitiesActions.Actions.DeleteCityData))
    .pipe(switchMap((action: Action) => {
      const id: string = action['payload'];
      this.CityapiService.deleteCity(id).subscribe();
      return [];
    }));

  @Effect()
  addCityData$: any = this.actions.pipe(ofType(CitiesActions.Actions.AddCityData))
    .pipe(switchMap((action: Action) => {
      const cityData: CityInterface = action['payload'];
      this.CityapiService.addCity(cityData).subscribe();
      return [];
    }));

  @Effect()
  loadCityInfoData$: any = this.actions.pipe(ofType(CitiesActions.Actions.LoadActiveCityData))
    .pipe(switchMap((action: Action) => {
      const id: string = action['payload'];
      if (id) {
        this.getCitySubscription = this.CityapiService.getCity(id).subscribe((city: CityInterface) => {
          this.store.dispatch(new CitiesActions.SetActiveCityDataAction(city));
        });
      } else {
        this.store.dispatch(new CitiesActions.SetActiveCityDataAction(null));
        if (this.getCitySubscription) {
          this.getCitySubscription.unsubscribe();
          this.getCitySubscription = null;
        }
      }
      return [];
    }));

  @Effect()
  updateActiveCityData$: any = this.actions.pipe(ofType(CitiesActions.Actions.UpdateActiveCityData))
    .pipe(switchMap((action: Action) => {
      const payload: UpdateCityActionInterface = action['payload'];
      this.CityapiService.updateCity(payload.cityData, payload.id).subscribe();
      return [];
    }));
}
