import { Injectable } from "@angular/core";
import { Resolve } from '@angular/router';
import { Store } from '@ngrx/store';
import { TemplarState } from '../state-management';
import { Observable } from 'rxjs';
import { Actions, ofType } from '@ngrx/effects';
import { take } from 'rxjs/operators';
import { StylesActions } from '../state-management/states/styles';

@Injectable()
export class StylesDataResolver implements Resolve<void> {
  constructor(private store: Store<TemplarState>, private actions: Actions) { }

  resolve(): Observable<void> {
   this.store.dispatch(new StylesActions.LoadStylesDataAction());
   return this.actions.pipe(ofType(StylesActions.Actions.SetStylesData), take(1));
  }
}