import { Action } from '@ngrx/store';
import { SightInterface } from 'src/modules/dashboard/interfaces';
import { UpdateSightActionInterface } from '../../interfaces/update-sight-actions.interface';

export namespace SightActions {

  export enum Actions {
    LoadSightsData = '[Sight] LoadSightsData',
    SetSightsData = '[Sight] SetSightsData',
    SetActiveSightData = '[Sight] SetActiveSightData',
    DeleteSightData = '[Sight] DeleteSightData',
    AddSightData = '[Sight] AddSightData',
    LoadActiveSightData = '[Sight] LoadActiveSightData',
    UpdateActiveSightData = '[Sight] UpdateActiveSightData'
  }

  export class LoadSightsDataAction implements Action {
    readonly type: Actions = Actions.LoadSightsData;
    constructor() { }
  }

  export class LoadActiveSightDataAction implements Action {
    readonly type: Actions = Actions.LoadActiveSightData;
    constructor(public payload: string) { }
  }

  export class SetSightsDataAction implements Action {
    readonly type: Actions = Actions.SetSightsData;
    constructor(public payload: SightInterface[]) { }
  }

  export class SetActiveSightDataAction implements Action {
    readonly type: Actions = Actions.SetActiveSightData;
    constructor(public payload: SightInterface) { }
  }

  export class UpdateActiveSightDataAction implements Action {
    readonly type: Actions = Actions.UpdateActiveSightData;
    constructor(public payload: UpdateSightActionInterface) { }
  }

  export class DeleteSightDataAction implements Action {
    readonly type: Actions = Actions.DeleteSightData;
    constructor(public payload: string) { }
  }

  export class AddSightDataAction implements Action {
    readonly type: Actions = Actions.AddSightData;
    constructor(public payload: SightInterface) { }
  }

  export type SightsActionsType = LoadSightsDataAction |
                                  LoadActiveSightDataAction |
                                  SetSightsDataAction |
                                 SetActiveSightDataAction |
                                 UpdateActiveSightDataAction |
                                 DeleteSightDataAction |
                                 AddSightDataAction;
}
