import { Injectable } from '@angular/core';
import { AngularFireStorage } from 'angularfire2/storage';
import { finalize, mergeMap } from 'rxjs/operators';
import { Subject, Observable } from 'rxjs';
import { FileInterface } from '../interfaces/file.interface';

@Injectable()
export class UploadService {

  constructor(private storage: AngularFireStorage) {
  }

  uploadImage(event: FileList, folder: string): Observable<FileInterface> {
    return this.upload(event.item(0), folder);
  }

  uploadAudio(event: FileList, folder: string): Observable<FileInterface> {
    return this.upload(event.item(0), folder);
  }

  private upload(file: File, folder: string): Observable<FileInterface> {
    const path = `${folder}/${new Date().getTime()}_${file.name}`;
    const subject: Subject<FileInterface> = new Subject<FileInterface>();
    const task = this.storage.upload(path, file);
    task.snapshotChanges().pipe(
      finalize(() => {
        this.storage.ref(path).getDownloadURL().subscribe((url: string) => {
          subject.next({ url, fileName: file.name });
        })
      })
    ).subscribe();
    return subject;
  }
}
