import { AudiosStateInterface, StylesStateInterface } from '../../interfaces';
import { StylesActions } from './styles.actions';
import { AudioInterface, StyleInterface } from 'src/modules/dashboard/interfaces';

const initialState: StylesStateInterface = {
  styles: []
};

export function stylesReducer(state: StylesStateInterface = initialState, action: StylesActions.StylesActionsType): StylesStateInterface {
  switch (action.type) {
    case StylesActions.Actions.SetStylesData: {
      return { ...state, styles: <StyleInterface[]>action["payload"] };
    }
    default: {
      return state;
    }
  }
}
