import { Action } from '@ngrx/store';
import { AudioInterface } from 'src/modules/dashboard/interfaces';

export namespace AudiosActions {

  export enum Actions {
    LoadAudiosData = '[Audios] LoadAudiosData',
    SetAudiosData = '[Audios] SetAudiosData',
    DeleteAudioData = '[Audios] DeleteAudioData',
    AddAudioData = '[Audios] AddAudioData',
    AudioDataAdded = '[Audios] AudioDataAdded'
  }

  export class LoadAudiosDataAction implements Action {
    readonly type: Actions = Actions.LoadAudiosData;
    constructor() { }
  }

  export class SetAudiosDataAction implements Action {
    readonly type: Actions = Actions.SetAudiosData;
    constructor(public payload: AudioInterface[]) { }
  }

  export class DeleteAudioDataAction implements Action {
    readonly type: Actions = Actions.DeleteAudioData;
    constructor(public payload: string) { }
  }

  export class AddAudioDataAction implements Action {
    readonly type: Actions = Actions.AddAudioData;
    constructor(public payload: AudioInterface) { }
  }

  export class AudioDataAddedAction implements Action {
    readonly type: Actions = Actions.AudioDataAdded;
    constructor(public payload: string) { }
  }

  export type AudiosActionsType = LoadAudiosDataAction |
                                  SetAudiosDataAction |
                                  DeleteAudioDataAction |
                                  AddAudioDataAction |
                                  AudioDataAddedAction;
}
