import { Injectable } from "@angular/core";
import { Resolve } from '@angular/router';
import { Store } from '@ngrx/store';
import { TemplarState } from '../state-management';
import { Observable } from 'rxjs';
import { RouteActions } from '../state-management/states/route';
import { Actions, ofType } from '@ngrx/effects';
import { take } from 'rxjs/operators';

@Injectable()
export class RoutesDataResolver implements Resolve<void> {
  constructor(private store: Store<TemplarState>, private actions: Actions) { }

  resolve(): Observable<void> {
    this.store.dispatch(new RouteActions.LoadRoutesDataAction());
    return this.actions.pipe(ofType(RouteActions.Actions.SetRoutesData), take(1));
  }
}