import { Component, OnInit, Input, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Observable } from "rxjs/Observable";
import 'rxjs/add/observable/of';
import { TokenInterface } from '../../interfaces';
import { SignInService } from '../../services/sign-in.service';
import { routerNgProbeToken } from '@angular/router/src/router_module';

const USER_ACCOUNT: string = '123';
const USER_PASSWORD: string = '123';

@Component({
  selector: 'app-sign',
  templateUrl: './sign.component.html',
  styleUrls: ['./sign.component.scss']
})
export class SignComponent {
  token: boolean = false;
  signInForm: FormGroup;

  constructor(private router: Router, private formBuilder: FormBuilder, private signInService: SignInService) {
    this.signInForm = this.formBuilder.group({
      account: ['', [
        Validators.required,
        Validators.minLength(3)
      ]],
      password: ['', [
        Validators.required,
        Validators.minLength(3)
      ]]
    });
  }

  logIn(): void {
    if (this.signInForm.value.account == USER_ACCOUNT && this.signInForm.value.password == USER_PASSWORD) {
      this.signInService.setToken(this.signInForm.value.account);
      this.router.navigate(['']);
    }
  }
}
