import { Injectable } from '@angular/core';
import { Effect, Actions, ofType } from '@ngrx/effects';
import { Action, Store } from '@ngrx/store';
import { Subscription } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { StylesActions } from './styles.actions';
import { StyleInterface } from 'src/modules/dashboard/interfaces';
import { TemplarState } from '../../interfaces';
import { StylesApiService } from 'src/modules/dashboard/services/styles-api.service';

@Injectable()
export class StylesEffects {

  getCitySubscription: Subscription;

  constructor(
    private actions: Actions,
    private stylesApiService: StylesApiService,
    private store: Store<TemplarState>
  ) { }

  @Effect()
  loadStylesData$: any = this.actions.pipe(ofType(StylesActions.Actions.LoadStylesData))
    .pipe(switchMap((action: Action) => {
      this.stylesApiService.getStyles().subscribe((styles: StyleInterface[]) => {
        this.store.dispatch(new StylesActions.SetStylesDataAction(styles));
      });
      return [];
    }));
}
