import { AngularFirestoreCollection, AngularFirestore, AngularFirestoreDocument } from 'angularfire2/firestore';
import { CityInterface } from '../interfaces';
import { map } from 'rxjs/operators';
import { Observable, from } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable()
export class CityApiService {
  private citysCollection: AngularFirestoreCollection<CityInterface> = this.afs.collection<CityInterface>('city');
  constructor(private afs: AngularFirestore) { }
  getCities(): Observable<CityInterface[]> {
    return this.citysCollection.snapshotChanges().pipe(
      map(actions => actions.map(a => {
        const data = a.payload.doc.data() as CityInterface;
        const id = a.payload.doc.id;
        return { id, ...data };
      }))
    )
  }
  getCity(id: string): Observable<CityInterface> {
    const itemDoc = this.afs.doc<CityInterface>(`city/${id}`);
    return itemDoc.valueChanges().pipe(
      map((city: CityInterface) => {
        return { id, ...city };
      })
    );
  }
  updateCity(newCity: CityInterface, id: string): Observable<void> {
    const itemDoc = this.afs.doc<CityInterface>(`city/${id}`);
    const _newCity = { ...newCity };
    delete _newCity.id;
    return from(itemDoc.update(_newCity));
  }
  deleteCity(id: string): Observable<void> {
    const userDoc: AngularFirestoreDocument<CityInterface> = this.afs.doc(`city/${id}`);
    return from(userDoc.delete());
  }
  addCity(city: CityInterface): Observable<void> {
    return from(this.citysCollection.add(city)).pipe(
      map(() => { })
    );
  }
}