import { Injectable } from '@angular/core';
import { TokenInterface } from '../interfaces';

const TOKEN_KEY: string = 'user_token';

@Injectable()
export class SignInService {
  getToken(): TokenInterface {
    const tokenEdge: string = localStorage.getItem(TOKEN_KEY);
    return tokenEdge ? JSON.parse(tokenEdge, this.dateTimeReviver) : null;
  }

  setToken(userName: string): void {
    const date: Date = new Date();
    date.setHours(date.getHours() + 3);
    const token: TokenInterface = { userName: userName, expiresIn: date };
    localStorage.setItem(TOKEN_KEY, JSON.stringify(token));
  }

  removeToken(): void {
    localStorage.removeItem(TOKEN_KEY);
  }

  private dateTimeReviver(key, value): any {
    return key === 'expiresIn' ? new Date(value) : value;
  }
}