import { Component } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { takeUntil } from 'rxjs/operators';
import { RouteInterface } from '../../interfaces';
import { Store } from '@ngrx/store';
import { TemplarState, routesSelectors } from '../../state-management';
import { AbstractComponent } from 'src/modules/shared';
import { RouteActions } from '../../state-management/states/route';
import { Router } from '@angular/router';

@Component({
  selector: 'app-route',
  templateUrl: './route.component.html',
  styleUrls: ['./route.component.scss']
})
export class RouteComponent extends AbstractComponent {

  routes$: Observable<RouteInterface[]> = this.store.select(routesSelectors.getRoutesSelector).pipe(takeUntil(this.destroyed$));

  constructor(private router: Router, private store: Store<TemplarState>) {
    super();
  }

  onAddItem(): void {
    this.router.navigate(['addroute']);
  }

  onEditItem(item: RouteInterface): void {
    this.router.navigate([`route-info/${item.id}`]);
  }

  onDeleteItem(item: RouteInterface): void {
    confirm(`Delete "${item.name._en}"?`) ? this.store.dispatch(new RouteActions.DeleteRouteDataAction(item.id)) : null;
  }
}
