import { Injectable } from "@angular/core";
import { Resolve } from '@angular/router';
import { Store } from '@ngrx/store';
import { TemplarState } from '../state-management';
import { Observable } from 'rxjs';
import { AudiosActions } from '../state-management/states/audios';
import { Actions, ofType } from '@ngrx/effects';
import { take } from 'rxjs/operators';

@Injectable()
export class AudiosDataResolver implements Resolve<void> {
  constructor(private store: Store<TemplarState>, private actions: Actions) { }

  resolve(): Observable<void> {
    this.store.dispatch(new AudiosActions.LoadAudiosDataAction());
    return this.actions.pipe(ofType(AudiosActions.Actions.SetAudiosData), take(1));
  }
}
