import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from '././guards/auth.guard'
import { signInRoutes } from '../modules/sign-in/sign-in-routing.module';
import { dashboardRoutes } from 'src/modules/dashboard/dashboard-routing.module';

const routes: Routes = [
  { path: '', children: dashboardRoutes, canActivate: [AuthGuard] },
  { path: 'signin', children: signInRoutes },
  { path: '**', redirectTo: '', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [AuthGuard]
})
export class AppRoutingModule { }