import { Component } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { takeUntil } from 'rxjs/operators';
import { CityInterface, AccesscodesInterface } from '../../interfaces';
import { Store } from '@ngrx/store';
import { TemplarState, accesscodesSelectors } from '../../state-management';
import { AbstractComponent } from 'src/modules/shared';
import { Router } from '@angular/router';
import { AccesscodesActions } from '../../state-management/states/accesscodes';

@Component({
  selector: 'app-accesscodes',
  templateUrl: './accesscodes.component.html',
  styleUrls: ['./accesscodes.component.scss']
})
export class AccesscodesComponent extends AbstractComponent {

  accesscodes$: Observable<AccesscodesInterface[]> = this.store.select(accesscodesSelectors.getAccesscodesSelector).pipe(takeUntil(this.destroyed$));

  constructor(private router: Router, private store: Store<TemplarState>) {
    super();
  }

  onAddItem(): void {
    this.router.navigate(['addaccesscodes']);
  }

  onEditItem(item: AccesscodesInterface): void {
    this.router.navigate([`accesscodes-info/${item.id}`]);
  }

  onDeleteItem(item: AccesscodesInterface): void {
    this.store.dispatch(new AccesscodesActions.DeleteAccesscodesDataAction(item.id));
  }
}
