import { RouteStateInterface } from '../../interfaces';
import { RouteInterface } from 'src/modules/dashboard/interfaces';
import { RouteActions } from './route.actions';


const initialState: RouteStateInterface = {
  routes: [],
  activeRoute: null
};

export function routeReducer(state: RouteStateInterface = initialState, action: RouteActions.RoutesActionsType): RouteStateInterface {
  switch (action.type) {
    case RouteActions.Actions.SetRoutesData: {
      return { ...state, routes: <RouteInterface[]>action["payload"] };
    }
    case RouteActions.Actions.SetActiveRouteData: {
      return { ...state, activeRoute: <RouteInterface>action["payload"] };
    }
    default: {
      return state;
    }
  }
}
