import { AngularFirestoreCollection, AngularFirestore, AngularFirestoreDocument } from 'angularfire2/firestore';
import { RouteInterface } from '../interfaces';
import { map } from 'rxjs/operators';
import { Observable, from } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable()
export class RouteApiService {
  private RoutesCollection: AngularFirestoreCollection<RouteInterface> = this.afs.collection<RouteInterface>('itineraries');
  constructor(private afs: AngularFirestore) { }
  getRoutes(): Observable<RouteInterface[]> {
    return this.RoutesCollection.snapshotChanges().pipe(
      map(actions => actions.map(a => {
        const data = a.payload.doc.data() as RouteInterface;
        const id = a.payload.doc.id;
        return { id, ...data };
      }))
    )
  }
  getRoute(id: string): Observable<RouteInterface> {
    const itemDoc = this.afs.doc<RouteInterface>(`itineraries/${id}`);
    return itemDoc.valueChanges().pipe(
      map((route: RouteInterface) => {
        return { id, ...route };
      })
    );
  }
  updateRoute(newRoute: RouteInterface, id: string): Observable<void> {
    const itemDoc = this.afs.doc<RouteInterface>(`itineraries/${id}`);
    const _newRoute = { ...newRoute };
    delete _newRoute.id;
    return from(itemDoc.update(_newRoute));
  }
  deleteRoute(id: string): Observable<void> {
    const userDoc: AngularFirestoreDocument<RouteInterface> = this.afs.doc(`itineraries/${id}`);
    return from(userDoc.delete());
  }
  addRoute(route: RouteInterface): Observable<void> {
    return from(this.RoutesCollection.add(route)).pipe(
      map(() => { })
    );
  }
}