import { AngularFirestoreCollection, AngularFirestore, AngularFirestoreDocument } from 'angularfire2/firestore';
import { SightInterface } from '../interfaces';
import { map } from 'rxjs/operators';
import { Observable, from } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable()
export class SightApiService {
  private sightsCollection: AngularFirestoreCollection<SightInterface> = this.afs.collection<SightInterface>('sights');
  constructor(private afs: AngularFirestore) { }
  getSights(): Observable<SightInterface[]> {
    return this.sightsCollection.snapshotChanges().pipe(
      map(actions => actions.map(a => {
        const data = a.payload.doc.data() as SightInterface;
        const id = a.payload.doc.id;
        return { id, ...data };
      }))
    )
  }
  getSight(id: string): Observable<SightInterface> {
    const itemDoc = this.afs.doc<SightInterface>(`sights/${id}`);
    return itemDoc.valueChanges().pipe(
      map((sight: SightInterface) => {
        return { id, ...sight };
      })
    );
  }
  updateSight(newSight: SightInterface, id: string): Observable<void> {
    const itemDoc = this.afs.doc<SightInterface>(`sights/${id}`);
    const _newSight = { ...newSight };
    delete _newSight.id;
    return from(itemDoc.update(_newSight));
  }
  deleteSight(id: string): Observable<void> {
    const userDoc: AngularFirestoreDocument<SightInterface> = this.afs.doc(`sights/${id}`);
    return from(userDoc.delete());
  }
  addSight(sight: SightInterface): Observable<void> {
    return from(this.sightsCollection.add(sight)).pipe(
      map(() => { })
    );
  }
}