import { Injectable } from '@angular/core';
import { Effect, Actions, ofType } from '@ngrx/effects';
import { Action, Store } from '@ngrx/store';
import { Subscription } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { AudiosActions } from './audios.actions';
import { AudiosApiService } from 'src/modules/dashboard/services';
import { AudioInterface } from 'src/modules/dashboard/interfaces';
import { TemplarState } from '../../interfaces';

@Injectable()
export class AudiosEffects {

  getCitySubscription: Subscription;

  constructor(
    private actions: Actions,
    private audiosApiService: AudiosApiService,
    private store: Store<TemplarState>
  ) { }

  @Effect()
  loadAudiosData$: any = this.actions.pipe(ofType(AudiosActions.Actions.LoadAudiosData))
    .pipe(switchMap((action: Action) => {
      this.audiosApiService.getAudios().subscribe((audios: AudioInterface[]) => {
        this.store.dispatch(new AudiosActions.SetAudiosDataAction(audios));
      });
      return [];
    }));

  @Effect()
  deleteAudioData$: any = this.actions.pipe(ofType(AudiosActions.Actions.DeleteAudioData))
    .pipe(switchMap((action: Action) => {
      const id: string = action['payload'];
      this.audiosApiService.deleteAudio(id).subscribe();
      return [];
    }));

  @Effect()
  addAudioData$: any = this.actions.pipe(ofType(AudiosActions.Actions.AddAudioData))
    .pipe(switchMap((action: Action) => {
      const audioData: AudioInterface = action['payload'];
      this.audiosApiService.addAudio(audioData).subscribe((id: string) => {
        this.store.dispatch(new AudiosActions.AudioDataAddedAction(id));
      });
      return [];
    }));
}
