import { Injectable } from "@angular/core";
import { Resolve } from '@angular/router';
import { Store } from '@ngrx/store';
import { TemplarState } from '../state-management';
import { Observable } from 'rxjs';
import { Actions, ofType } from '@ngrx/effects';
import { take } from 'rxjs/operators';
import { AccesscodesActions } from '../state-management/states/accesscodes';

@Injectable()
export class AccesscodeDataResolver implements Resolve<void> {
  constructor(private store: Store<TemplarState>, private actions: Actions) { }

  resolve(): Observable<void> {
   this.store.dispatch(new AccesscodesActions.LoadAccesscodesDataAction());
   return this.actions.pipe(ofType(AccesscodesActions.Actions.SetAccesscodesData), take(1));
  }
}