import { Injectable } from "@angular/core";
import { Resolve } from '@angular/router';
import { Store } from '@ngrx/store';
import { TemplarState } from '../state-management';
import { Observable } from 'rxjs';
import { CitiesActions } from '../state-management/states/cities';
import { Actions, ofType } from '@ngrx/effects';
import { take } from 'rxjs/operators';

@Injectable()
export class CitiesDataResolver implements Resolve<void> {
  constructor(private store: Store<TemplarState>, private actions: Actions) { }

  resolve(): Observable<void> {
   this.store.dispatch(new CitiesActions.LoadCitiesDataAction());
   return this.actions.pipe(ofType(CitiesActions.Actions.SetCitiesData), take(1));
  }
}