import { AngularFirestoreCollection, AngularFirestore, AngularFirestoreDocument, DocumentReference } from 'angularfire2/firestore';
import { AudioInterface } from '../interfaces';
import { map } from 'rxjs/operators';
import { Observable, from } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable()
export class AudiosApiService {

  private audiosCollection: AngularFirestoreCollection<AudioInterface> = this.afs.collection<AudioInterface>('audios');

  constructor(private afs: AngularFirestore) { }

  getAudios(): Observable<AudioInterface[]> {
    return this.audiosCollection.snapshotChanges().pipe(
      map(actions => actions.map(a => {
        const data = a.payload.doc.data() as AudioInterface;
        const id = a.payload.doc.id;
        return { id, ...data };
      }))
    )
  }
  getAudio(id: string): Observable<AudioInterface> {
    const itemDoc = this.afs.doc<AudioInterface>(`audios/${id}`);
    return itemDoc.valueChanges().pipe(
      map((audio: AudioInterface) => {
        return { id, ...audio };
      })
    );
  }
  deleteAudio(id: string): Observable<void> {
    const userDoc: AngularFirestoreDocument<AudioInterface> = this.afs.doc(`audios/${id}`);
    return from(userDoc.delete());
  }
  addAudio(audio: AudioInterface): Observable<string> {
    return from(this.audiosCollection.add(audio)).pipe(
      map((audio: DocumentReference) => audio.id)
    );
  }
}
