import { Action } from '@ngrx/store';
import { StyleInterface } from 'src/modules/dashboard/interfaces';

export namespace StylesActions {

  export enum Actions {
    LoadStylesData = '[Styles] LoadAudiosData',
    SetStylesData = '[Styles] SetAudiosData',
    }

  export class LoadStylesDataAction implements Action {
    readonly type: Actions = Actions.LoadStylesData;
    constructor() { }
  }

  export class SetStylesDataAction implements Action {
    readonly type: Actions = Actions.SetStylesData;
    constructor(public payload: StyleInterface[]) { }
  }

    export type StylesActionsType = LoadStylesDataAction |
                                  SetStylesDataAction;
}
