import { Injectable } from '@angular/core';
import * as places from 'places.js'

declare var L: any;
var markers = [];
@Injectable()
export class MapsService {

  createMap(viewLat?: number, viewLng?: number): any {
    const zoom = viewLat !== null && viewLng !== null ? 13 : 1;
    const map = L.map('map').setView([viewLat || 0, viewLng || 0], zoom); //viewLat || 51.505, viewLng || -0.09
    L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}', {
      attribution: '',
      maxZoom: 18,
      id: 'mapbox.streets',
      accessToken: 'pk.eyJ1IjoiZ3puZ3puIiwiYSI6ImNqc3N4NDEwOTFpb3M0YW9lYzFnNW43NDUifQ.SGCPcbkRSOTsZaY2D6ALKQ'
    }).addTo(map);
    var osmLayer = new L.TileLayer(
      'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        minZoom: 1,
        maxZoom: 13,
      }
    );
    //map.setView(new L.LatLng(0, 0), 1);
    map.addLayer(osmLayer);

    var placesAutocomplete = places({
      appId: 'plNV2TW92I8R',
      apiKey: '3e9387d7ab0dcfe0fcbb0bbd59605779',
      container: document.querySelector('.location-input')
    });
    markers = [];

    placesAutocomplete.on('suggestions', handleOnSuggestions);
    placesAutocomplete.on('cursorchanged', handleOnCursorchanged);
    placesAutocomplete.on('change', handleOnChange);
    placesAutocomplete.on('clear', handleOnClear);

    function handleOnSuggestions(e) {
      markers.forEach(removeMarker);
      markers = [];

      if (e.suggestions.length === 0) {
        map.setView(new L.LatLng(0, 0), 1);
        return;
      }

      e.suggestions.forEach(addMarker);
      findBestZoom();
    }

    function handleOnChange(e) {
      markers
        .forEach(function (marker, markerIndex) {
          if (markerIndex === e.suggestionIndex) {
            markers = [marker];
            marker.setOpacity(1);
            findBestZoom();
          } else {
            removeMarker(marker);
          }
        });
    }

    function handleOnClear() {
      map.setView(new L.LatLng(0, 0), 1);
      markers.forEach(removeMarker);
    }

    function handleOnCursorchanged(e) {
      markers
        .forEach(function (marker, markerIndex) {
          if (markerIndex === e.suggestionIndex) {
            marker.setOpacity(1);
            marker.setZIndexOffset(1000);
          } else {
            marker.setZIndexOffset(0);
            marker.setOpacity(0.5);
          }
        });
    }

    function addMarker(suggestion) {
      var marker = L.marker(suggestion.latlng, { opacity: .4 });
      //marker.addTo(map);
      markers.push(marker);
    }

    function removeMarker(marker) {
      map.removeLayer(marker);
    }

    function findBestZoom() {
      var featureGroup = L.featureGroup(markers);
      map.fitBounds(featureGroup.getBounds().pad(0.5), { animate: false });
    }

    return map;
  }



  setMarker(map: any, marker: any, lat: number, lng: number): any {
    if (marker) {
      const latlng = L.latLng(lat, lng);
      marker.setLatLng(latlng);
      return marker;
    } else {
      return L.marker([lat, lng]).addTo(map);
    }
  }
}
