import { Injectable } from "@angular/core";
import { Resolve } from '@angular/router';
import { Store } from '@ngrx/store';
import { TemplarState } from '../state-management';
import { Observable } from 'rxjs';
import { SightActions } from '../state-management/states/sight';
import { Actions, ofType } from '@ngrx/effects';
import { take } from 'rxjs/operators';

@Injectable()
export class SightsDataResolver implements Resolve<void> {
  constructor(private store: Store<TemplarState>, private actions: Actions) { }

  resolve(): Observable<void> {
    this.store.dispatch(new SightActions.LoadSightsDataAction());
    return this.actions.pipe(ofType(SightActions.Actions.SetSightsData), take(1));
  }
}
