import { Injectable } from '@angular/core';
import { Effect, Actions, ofType } from '@ngrx/effects';
import { Action, Store } from '@ngrx/store';
import { combineLatest, identity, Subscription } from 'rxjs';
import { switchMap, mergeMap } from 'rxjs/operators';
import { AccesscodesActions } from './accesscodes.actions';
import { AccesscodesApiService } from 'src/modules/dashboard/services';
import { AccesscodesInterface } from 'src/modules/dashboard/interfaces';
import { TemplarState } from '../../interfaces';
import { UpdateAccesscodesActionInterface } from '../../interfaces/update-accesscodes-actions.interface';

@Injectable()
export class AccesscodesEffects {
  getAccesscodesSubscription: Subscription;
  constructor(private actions: Actions,
    private AccesscodesapiService: AccesscodesApiService,
    private store: Store<TemplarState>
  ) { }

  @Effect()
  loadAccesscodesData$: any = this.actions.pipe(ofType(AccesscodesActions.Actions.LoadAccesscodesData))
    .pipe(switchMap((action: Action) => {
      this.AccesscodesapiService.getAccesscodes().subscribe((cities: AccesscodesInterface[]) => {
        this.store.dispatch(new AccesscodesActions.SetAccesscodesDataAction(cities));
      });
      return [];
    }));

  @Effect()
  deleteAccesscodesData$: any = this.actions.pipe(ofType(AccesscodesActions.Actions.DeleteAccesscodesData))
    .pipe(switchMap((action: Action) => {
      const id: string = action['payload'];
      this.AccesscodesapiService.deleteAccesscodes(id).subscribe();
      return [];
    }));

  @Effect()
  addAccesscodesData$: any = this.actions.pipe(ofType(AccesscodesActions.Actions.AddAccesscodesData))
    .pipe(switchMap((action: Action) => {
      const cityData: AccesscodesInterface = action['payload'];
      this.AccesscodesapiService.addAccesscodes(cityData).subscribe();
      return [];
    }));

  @Effect()
  loadAccesscodesInfoData$: any = this.actions.pipe(ofType(AccesscodesActions.Actions.LoadActiveAccesscodesData))
    .pipe(switchMap((action: Action) => {
      const id: string = action['payload'];
      if (id) {
        this.getAccesscodesSubscription = this.AccesscodesapiService.getAccesscod(id).subscribe((city: AccesscodesInterface) => {
          this.store.dispatch(new AccesscodesActions.SetActiveAccesscodDataAction(city));
        });
      } else {
        this.store.dispatch(new AccesscodesActions.SetActiveAccesscodDataAction(null));
        if (this.getAccesscodesSubscription) {
          this.getAccesscodesSubscription.unsubscribe();
          this.getAccesscodesSubscription = null;
        }
      }
      return [];
    }));

  @Effect()
  updateActiveAccesscodesData$: any = this.actions.pipe(ofType(AccesscodesActions.Actions.UpdateActiveAccesscodesData))
    .pipe(switchMap((action: Action) => {
      const payload: UpdateAccesscodesActionInterface = action['payload'];
      this.AccesscodesapiService.updateAccesscode(payload.accesscodesData, payload.id).subscribe();
      return [];
    }));
}
