import { Injectable } from '@angular/core';
import { Effect, Actions, ofType } from '@ngrx/effects';
import { Action, Store } from '@ngrx/store';
import { combineLatest, identity, Subscription } from 'rxjs';
import { switchMap, mergeMap } from 'rxjs/operators';
import { RouteActions } from './route.actions';
import { RouteInterface } from 'src/modules/dashboard/interfaces';
import { TemplarState } from '../../interfaces';
import { RouteApiService } from 'src/modules/dashboard/services/routeapi.service';
import { UpdateRouteActionInterface } from '../../interfaces/update-route-actions.interface';

@Injectable()
export class RoutesEffects {
  getRouteSubscription: Subscription;
  constructor(private actions: Actions,
    private RouteApiService: RouteApiService,
    private store: Store<TemplarState>
  ) { }

  @Effect()
  loadRoutesData$: any = this.actions.pipe(ofType(RouteActions.Actions.LoadRoutesData))
    .pipe(switchMap((action: Action) => {
      this.RouteApiService.getRoutes().subscribe((route: RouteInterface[]) => {
        this.store.dispatch(new RouteActions.SetRoutesDataAction(route));
      });
      return [];
    }));

  @Effect()
  deleteRouteData$: any = this.actions.pipe(ofType(RouteActions.Actions.DeleteRouteData))
    .pipe(switchMap((action: Action) => {
      const id: string = action['payload'];
      this.RouteApiService.deleteRoute(id).subscribe();
      return [];
    }));

  @Effect()
  addRouteData$: any = this.actions.pipe(ofType(RouteActions.Actions.AddRouteData))
    .pipe(switchMap((action: Action) => {
      const routeData: RouteInterface = action['payload'];
      this.RouteApiService.addRoute(routeData).subscribe();
      return [];
    }));

  @Effect()
  loadRouteInfoData$: any = this.actions.pipe(ofType(RouteActions.Actions.LoadActiveRouteData))
    .pipe(switchMap((action: Action) => {
      const id: string = action['payload'];
      if (id) {
        this.getRouteSubscription = this.RouteApiService.getRoute(id).subscribe((route: RouteInterface) => {
          this.store.dispatch(new RouteActions.SetActiveRouteDataAction(route));
        });
      } else {
        this.store.dispatch(new RouteActions.SetActiveRouteDataAction(null));
        if (this.getRouteSubscription) {
          this.getRouteSubscription.unsubscribe();
          this.getRouteSubscription = null;
        }
      }
      return [];
    }));

  @Effect()
  updateRouteRouteData$: any = this.actions.pipe(ofType(RouteActions.Actions.UpdateActiveRouteData))
    .pipe(switchMap((action: Action) => {
      const payload: UpdateRouteActionInterface = action['payload'];
      this.RouteApiService.updateRoute(payload.routeData, payload.id).subscribe();
      return [];
    }));
}
