import { Injectable } from "@angular/core";
import { Resolve, ActivatedRouteSnapshot } from '@angular/router';
import { Store } from '@ngrx/store';
import { TemplarState } from '../state-management';
import { Observable } from 'rxjs';
import { SightActions } from '../state-management/states/sight';
import { Actions, ofType } from '@ngrx/effects';
import { take } from 'rxjs/operators';

@Injectable()
export class SightInfoResolver implements Resolve<void> {
  constructor(private store: Store<TemplarState>, private actions: Actions) { }

  resolve(route: ActivatedRouteSnapshot): Observable<void> {
    this.store.dispatch(new SightActions.LoadActiveSightDataAction(route.params.id));
    return this.actions.pipe(ofType(SightActions.Actions.SetActiveSightData), take(1));
  }
}