import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { SignInService } from 'src/modules/sign-in/services';
import { TokenInterface } from 'src/modules/sign-in/interfaces';

@Injectable()
export class AuthGuard implements CanActivate {
  constructor(private signInService: SignInService, private router: Router) { }
  canActivate(): boolean {
    const token: TokenInterface = this.signInService.getToken();
    const loggedIn = token && token.expiresIn.getTime() > new Date().getTime();
    if (!loggedIn) {
      this.router.navigate(['/signin']);
    }
    return loggedIn;
  }
}
