import { AfterViewInit, Component, OnInit, Renderer2 } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Store } from '@ngrx/store';
import 'rxjs/add/operator/filter';
import { Observable } from 'rxjs/Observable';
import { map, take, takeUntil } from 'rxjs/operators';
import { AbstractComponent } from 'src/modules/shared';
import { CityInterface, SightInterface } from '../../interfaces';
import { FileInterface } from '../../interfaces/file.interface';
import { MapsService, UploadService } from '../../services';
import { citiesSelectors, sightSelectors, TemplarState } from '../../state-management';
import { CitiesActions } from '../../state-management/states/cities';

declare var L: any;

@Component({
  selector: 'app-city-info',
  templateUrl: './city-info.component.html',
  styleUrls: ['./city-info.component.scss']
})
export class CityInfoComponent extends AbstractComponent implements OnInit {
  editCity: boolean;
  updateCityForm: FormGroup;
  map: any;
  marker: any;
  uploading: boolean;

  city$: Observable<CityInterface> = this.store.select(citiesSelectors.getActiveCitySelector).pipe(takeUntil(this.destroyed$));
  sights$: Observable<SightInterface[]> = this.store.select(sightSelectors.getSightsSelector).pipe(takeUntil(this.destroyed$));

  get currentSights$(): Observable<SightInterface[]> {
    return this.sights$.pipe(map((sights: SightInterface[]) => {
      return sights ?
        sights.filter((sight: SightInterface) => {
          return this.updateCityForm.value && this.updateCityForm.value.sight_list && this.updateCityForm.value.sight_list.indexOf(sight.id) !== -1;
        }) : [];
    }));
  }

  constructor(
    private store: Store<TemplarState>,
    private router: Router,
    private formBuilder: FormBuilder,
    private activateRoute: ActivatedRoute,
    private mapsService: MapsService,
    private uploadService: UploadService,
    private renderer: Renderer2
  ) {
    super();
    //en, es, pt, de, fr, it
    this.updateCityForm = this.formBuilder.group({
      name: this.formBuilder.group({
        _en: ['', [
          Validators.required,
          Validators.minLength(3)
        ]],
        _es: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _pt: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _de: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _fr: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _it: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]]
      }),
      position: ['', [
        //Validators.required
      ]],
      desc: this.formBuilder.group({
        _en: ['', [
          Validators.required,
          Validators.minLength(3)
        ]],
        _es: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _pt: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _de: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _fr: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _it: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]]
      }),
      background_url: ['', [
        Validators.required,
        Validators.minLength(3)
      ]],
      location_lat: ['', [
        Validators.required
      ]],
      location_lng: ['', [
        Validators.required
      ]],
      sight_list: []
    })

    this.city$.subscribe((city: CityInterface) => {
      city ? this.updateCityForm.patchValue(city) : this.updateCityForm.reset();
    });

    this.updateCityForm.valueChanges.pipe(takeUntil(this.destroyed$)).subscribe((value: CityInterface) => {
      if (this.marker) {
        if (value.location_lat !== this.marker._latlng.lat || value.location_lng !== this.marker._latlng.lng) {
          this.marker = this.mapsService.setMarker(this.map, this.marker, value.location_lat, value.location_lng);
        }
      }
    });
  }

  ngOnInit(): void {
    this.map = this.mapsService.createMap(this.updateCityForm.get('location_lat').value, this.updateCityForm.get('location_lng').value);
    this.map.on('click', (e: any) => {
      this.marker = this.mapsService.setMarker(this.map, this.marker, e.latlng.lat, e.latlng.lng);
      this.updateCityForm.get('location_lat').setValue(e.latlng.lat, { emitEvent: false });
      this.updateCityForm.get('location_lng').setValue(e.latlng.lng, { emitEvent: false });
    });

    this.city$.pipe().subscribe((city: CityInterface) => {
      this.editCity = !!city;
      if (city) {
        this.marker = this.mapsService.setMarker(this.map, this.marker, city.location_lat, city.location_lng);
      }
    });
  }

  ngOnDestroy() {
    this.store.dispatch(new CitiesActions.LoadActiveCityDataAction(null));
  }

  saveCity(): void {
    this.updateCityForm.value.position === null || "" || 0 ? this.updateCityForm.get('position').setValue(500) : null;

    if (this.editCity) {
      this.city$.pipe(take(1)).subscribe((city: CityInterface) => {
        this.store.dispatch(new CitiesActions.UpdateActiveCityDataAction({ cityData: { ...city, ...this.updateCityForm.value }, id: this.activateRoute.snapshot.paramMap.get('id') }));
      });
    } else {
      this.store.dispatch(new CitiesActions.AddCityDataAction(this.updateCityForm.value));
    }
    this.goBack();
  }

  goBack(): void {
    this.router.navigate(['/city']);
  }

  startUpload(event: FileList) {
    this.uploading = true;
    this.uploadService.uploadImage(event, 'cities/images').subscribe((file: FileInterface) => {
      this.updateCityForm.get('background_url').setValue(file.url);
      this.uploading = false;
    });
  }
  onAddressFocus(event: FocusEvent) {
    this.renderer.addClass((event.target as HTMLElement).parentElement.parentElement, 'input-group-focus');
  }

  onAddressBlur(event: FocusEvent) {
    this.renderer.removeClass((event.target as HTMLElement).parentElement.parentElement, 'input-group-focus');
  }
}
