import { AngularFirestoreCollection, AngularFirestore, AngularFirestoreDocument } from 'angularfire2/firestore';
import { AccesscodesInterface } from '../interfaces';
import { map } from 'rxjs/operators';
import { Observable, from } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable()
export class AccesscodesApiService {

  private accesscodesCollection: AngularFirestoreCollection<AccesscodesInterface> = this.afs.collection<AccesscodesInterface>('accesscodes');

  constructor(private afs: AngularFirestore) { }

  getAccesscodes(): Observable<AccesscodesInterface[]> {
    return this.accesscodesCollection.snapshotChanges().pipe(
      map(actions => actions.map(a => {
        const data = a.payload.doc.data() as AccesscodesInterface;
        const id = a.payload.doc.id;
        return { id, ...data };
      }))
    )
  }
  getAccesscod(id: string): Observable<AccesscodesInterface> {
    const itemDoc = this.afs.doc<AccesscodesInterface>(`accesscodes/${id}`);
    return itemDoc.valueChanges().pipe(
      map((city: AccesscodesInterface) => {
        return { id, ...city };
      })
    );
  }
  updateAccesscode(newAccesscod: AccesscodesInterface, id: string): Observable<void> {
    const itemDoc = this.afs.doc<AccesscodesInterface>(`accesscodes/${id}`);
    const _newAccesscod = { ...newAccesscod };
    delete _newAccesscod.id;
    return from(itemDoc.update(_newAccesscod));
  }
  deleteAccesscodes(id: string): Observable<void> {
    const userDoc: AngularFirestoreDocument<AccesscodesInterface> = this.afs.doc(`accesscodes/${id}`);
    return from(userDoc.delete());
  }
  addAccesscodes(city: AccesscodesInterface): Observable<void> {
    return from(this.accesscodesCollection.add(city)).pipe(
      map(() => { })
    );
  }
}
