import { Component } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { takeUntil } from 'rxjs/operators';
import { CityInterface } from '../../interfaces';
import { Store } from '@ngrx/store';
import { TemplarState, citiesSelectors } from '../../state-management';
import { AbstractComponent } from 'src/modules/shared';
import { CitiesActions } from '../../state-management/states/cities';
import { Router } from '@angular/router';

@Component({
  selector: 'app-city',
  templateUrl: './city.component.html',
  styleUrls: ['./city.component.scss']
})
export class CityComponent extends AbstractComponent {

  cities$: Observable<CityInterface[]> = this.store.select(citiesSelectors.getCitiesSelector).pipe(takeUntil(this.destroyed$));

  constructor(private router: Router, private store: Store<TemplarState>) {
    super();
  }

  onAddItem(): void {
    this.router.navigate(['addcity']);
  }

  onEditItem(item: CityInterface): void {
    this.router.navigate([`city-info/${item.id}`]);
  }

  onDeleteItem(item: CityInterface): void {
    confirm(`Delete "${item.name._en}"?`) ? this.store.dispatch(new CitiesActions.DeleteCityDataAction(item.id)) : null;
  }
}
