import { Component, OnInit, Renderer2 } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/filter';
import { takeUntil, take, map } from 'rxjs/operators';
import { AbstractComponent } from 'src/modules/shared';
import { TemplarState, routesSelectors, sightSelectors, AudiosActions, audioSelectors, styleSelectors } from '../../state-management';
import { Store } from '@ngrx/store';
import { RouteInterface, SightInterface, AudioInterface, StyleInterface, SelectItemInterface } from '../../interfaces';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { RouteActions } from '../../state-management/states/route';
import { MapsService, UploadService } from '../../services';
import { RouteLength } from '../../enums';
import { FileInterface } from '../../interfaces/file.interface';

@Component({
  selector: 'app-route-info',
  templateUrl: './route-info.component.html',
  styleUrls: ['./route-info.component.scss']
})
export class RouteInfoComponent extends AbstractComponent implements OnInit {
  editRoute: boolean;
  updateRouteForm: FormGroup;
  map: any;
  marker: any;
  uploadingBackground: boolean;
  uploadingAudio: boolean;
  RouteLength = RouteLength;
  dropdownList = [];
  selectedItems = [];
  dropdownSettings = {
    singleSelection: false,
    text: "Select styles",
    selectAllText: 'Select All',
    unSelectAllText: 'UnSelect All',
    enableSearchFilter: true,
    classes: ""
  };

  route$: Observable<RouteInterface> = this.store.select(routesSelectors.getActiveRouteSelector).pipe(takeUntil(this.destroyed$));
  sights$: Observable<SightInterface[]> = this.store.select(sightSelectors.getSightsSelector).pipe(takeUntil(this.destroyed$));
  audios$: Observable<AudioInterface[]> = this.store.select(audioSelectors.getAudiosSelector).pipe(takeUntil(this.destroyed$));
  styles$: Observable<StyleInterface[]> = this.store.select(styleSelectors.getStylesSelector).pipe(takeUntil(this.destroyed$))

  get routeLengths(): RouteLength[] {
    return Object.keys(RouteLength).map((key: string) => RouteLength[key]);
  }

  get currentSights$(): Observable<SightInterface[]> {
    return this.sights$.pipe(map((sights: SightInterface[]) => {
      return sights ?
        sights.filter((sight: SightInterface) => {
          return this.updateRouteForm.value && this.updateRouteForm.value.sights_list && this.updateRouteForm.value.sights_list.indexOf(sight.id) !== -1;
        }) : [];
    }));
  }

  get currentAudios$(): Observable<AudioInterface[]> {
    return this.audios$.pipe(map((audios: AudioInterface[]) => {
      return audios ?
        audios.filter((audio: AudioInterface) => {
          return this.updateRouteForm.value && this.updateRouteForm.value.audios_list && this.updateRouteForm.value.audios_list.indexOf(audio.id) !== -1;
        }) : [];
    }));
  }

  get currentStyles$(): Observable<StyleInterface[]> {
    return this.styles$.pipe(map((styles: StyleInterface[]) => {
      return styles ?
        styles.filter((style: StyleInterface) => {
          return this.updateRouteForm.value && this.updateRouteForm.value.style && this.updateRouteForm.value.style.indexOf(style.id) !== -1;
        }) : [];
    }));
  }

  constructor(
    private store: Store<TemplarState>,
    private router: Router,
    private formBuilder: FormBuilder,
    private activateRoute: ActivatedRoute,
    private mapsService: MapsService,
    private uploadService: UploadService,
    private renderer: Renderer2
  ) {
    super();

    this.updateRouteForm = this.formBuilder.group({
      name: this.formBuilder.group({
        _en: ['', [
          Validators.required,
          Validators.minLength(3)
        ]],
        _es: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _pt: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _de: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _fr: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _it: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]]
      }),
      desc: this.formBuilder.group({
        _en: ['', [
          Validators.required,
          Validators.minLength(3)
        ]],
        _es: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _pt: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _de: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _fr: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _it: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]]
      }),
      audios_list: [],
      contact_phone: ['', [
        Validators.required,
        Validators.minLength(6)
      ]],
      background_url: ['', [
        Validators.required,
        Validators.minLength(3)
      ]],
      hours: ['', [
        Validators.required
      ]],
      kilometers: ['', [
        Validators.required
      ]],
      leght: ['', [
        Validators.required
      ]],
      // location_lat: ['', [
      //   Validators.required
      // ]],
      // location_lng: ['', [
      //   Validators.required
      // ]],
      sights_list: [],
      style: []
    })

    this.route$.subscribe((route: RouteInterface) => {
      route ? this.updateRouteForm.patchValue(route) : this.updateRouteForm.reset();
    });

    // this.updateRouteForm.valueChanges.pipe(takeUntil(this.destroyed$)).subscribe((value: RouteInterface) => {
    //   if (this.marker) {
    //     if (value.location_lat !== this.marker._latlng.lat || value.location_lng !== this.marker._latlng.lng) {
    //       this.marker = this.mapsService.setMarker(this.map, this.marker, value.location_lat, value.location_lng);
    //     }
    //   }
    // });
  }

  ngOnInit() {
    // this.map = this.mapsService.createMap(this.updateRouteForm.get('location_lat').value, this.updateRouteForm.get('location_lng').value);
    // this.map.on('click', (e: any) => {
    //   this.marker = this.mapsService.setMarker(this.map, this.marker, e.latlng.lat, e.latlng.lng);
    //   this.updateRouteForm.get('location_lat').setValue(e.latlng.lat, { emitEvent: false });
    //   this.updateRouteForm.get('location_lng').setValue(e.latlng.lng, { emitEvent: false });
    // });

    this.route$.pipe().subscribe((route: RouteInterface) => {
      this.editRoute = !!route;
      //   if (route) {
      //     this.marker = this.mapsService.setMarker(this.map, this.marker, route.location_lat, route.location_lng);
      //   }
    });

    this.styles$.subscribe((data: StyleInterface[]) => {
      this.dropdownList = this.getSelectItems(data);
    });

    this.currentStyles$.pipe(take(1)).subscribe((styles: StyleInterface[]) => {
      this.selectedItems = this.getSelectItems(styles);
    });
  }

  ngOnDestroy() {
    this.store.dispatch(new RouteActions.LoadActiveRouteDataAction(null));
  }

  saveRoute() {
    if (this.editRoute) {
      this.route$.pipe(take(1)).subscribe((route: RouteInterface) => {
        this.store.dispatch(new RouteActions.UpdateActiveRouteDataAction({ routeData: { ...route, ...this.updateRouteForm.value }, id: this.activateRoute.snapshot.paramMap.get('id') }));
      });
    } else {
      this.store.dispatch(new RouteActions.AddRouteDataAction(this.updateRouteForm.value));
    }
    this.goBack();
  }

  goBack(): void {
    this.router.navigate(['/route']);
  }

  getLengthName(value: RouteLength): string {
    return Object.keys(RouteLength).find((key: string) => RouteLength[key] === value);
  }

  startUploadBackground(event: FileList) {
    this.uploadingBackground = true;
    this.uploadService.uploadImage(event, 'routes/images').subscribe((file: FileInterface) => {
      this.updateRouteForm.get('background_url').setValue(file.url);
      this.uploadingBackground = false;
    });
  }

  startUploadAudio(event: FileList) {
    this.uploadingAudio = true;
    this.uploadService.uploadAudio(event, 'routes/audio').subscribe((file: FileInterface) => {
      this.store.dispatch(new AudiosActions.AddAudioDataAction({ audio_content: file.url, audio_name: file.fileName }));
      //this.updateRouteForm.get('audi_url').setValue(url);
      this.uploadingAudio = false;
    });
  }

  // styles functions

  onItemSelect(item: SelectItemInterface): void {
    const style = this.updateRouteForm.value.style || [];
    style.push(item.id);
    this.updateRouteForm.get('style').setValue(style);
  }

  OnItemDeSelect(item: SelectItemInterface): void {
    let style = this.updateRouteForm.value.style || [];
    style = style.filter(id => id !== item.id);
    this.updateRouteForm.get('style').setValue(style);
  }

  onSelectAll(items: SelectItemInterface[]): void {
    this.updateRouteForm.get('style').setValue(items.map((item: SelectItemInterface) => item.id));
  }

  onDeSelectAll(items: SelectItemInterface[]): void {
    this.updateRouteForm.get('style').setValue([]);
  }

  private getSelectItems(data: StyleInterface[]): SelectItemInterface[] {
    return data.map((style: StyleInterface) => {
      return { itemName: style.name._en, id: style.id } as SelectItemInterface;
    });
  }
  onAddressFocus(event: FocusEvent) {
    this.renderer.addClass((event.target as HTMLElement).parentElement.parentElement, 'input-group-focus');
  }

  onAddressBlur(event: FocusEvent) {
    this.renderer.removeClass((event.target as HTMLElement).parentElement.parentElement, 'input-group-focus');
  }
}
