import { Action } from '@ngrx/store';
import { CityInterface } from 'src/modules/dashboard/interfaces';
import { UpdateCityActionInterface } from '../../interfaces/update-city-actions.interface';

export namespace CitiesActions {

  export enum Actions {
    LoadCitiesData = '[Cities] LoadCitiesData',
    SetCitiesData = '[Cities] SetCitiesData',
    SetActiveCityData = '[Cities] SetActiveCityData',
    DeleteCityData = '[Cities] DeleteCityData',
    AddCityData = '[Cities] AddCityData',
    LoadActiveCityData = '[Cities] LoadActiveCityData',
    UpdateActiveCityData = '[Cities] UpdateActiveCityData'
  }

  export class LoadCitiesDataAction implements Action {
    readonly type: Actions = Actions.LoadCitiesData;
    constructor() { }
  }

  export class LoadActiveCityDataAction implements Action {
    readonly type: Actions = Actions.LoadActiveCityData;
    constructor(public payload: string) { }
  }

  export class SetCitiesDataAction implements Action {
    readonly type: Actions = Actions.SetCitiesData;
    constructor(public payload: CityInterface[]) { }
  }

  export class SetActiveCityDataAction implements Action {
    readonly type: Actions = Actions.SetActiveCityData;
    constructor(public payload: CityInterface) { }
  }

  export class UpdateActiveCityDataAction implements Action {
    readonly type: Actions = Actions.UpdateActiveCityData;
    constructor(public payload: UpdateCityActionInterface) { }
  }

  export class DeleteCityDataAction implements Action {
    readonly type: Actions = Actions.DeleteCityData;
    constructor(public payload: string) { }
  }

  export class AddCityDataAction implements Action {
    readonly type: Actions = Actions.AddCityData;
    constructor(public payload: CityInterface) { }
  }

  export type CitiesActionsType = LoadCitiesDataAction |
                                 SetCitiesDataAction |
                                 SetActiveCityDataAction |
                                 DeleteCityDataAction |
                                 AddCityDataAction |
                                 LoadActiveCityDataAction |
                                 UpdateActiveCityDataAction;
}
