import { CityStateInterface } from '../../interfaces';
import { CitiesActions } from './cities.actions';
import { CityInterface } from 'src/modules/dashboard/interfaces';


const initialState: CityStateInterface = {
  cities: [],
  activeCity: null
};

export function citiesReducer(state: CityStateInterface = initialState, action: CitiesActions.CitiesActionsType): CityStateInterface {
  switch (action.type) {
    case CitiesActions.Actions.SetCitiesData: {
      return { ...state, cities: <CityInterface[]>action["payload"] };
    }
    case CitiesActions.Actions.SetActiveCityData: {
      return { ...state, activeCity: <CityInterface>action["payload"] };
    }
    default: {
      return state;
    }
  }
}