import { Injectable } from '@angular/core';
import { Effect, Actions, ofType } from '@ngrx/effects';
import { Action, Store } from '@ngrx/store';
import { combineLatest, identity, Subscription } from 'rxjs';
import { switchMap, mergeMap } from 'rxjs/operators';
import { SightActions } from './sight.actions';
import { SightInterface } from 'src/modules/dashboard/interfaces';
import { TemplarState } from '../../interfaces';
import { SightApiService } from 'src/modules/dashboard/services/sightapi.service';
import { UpdateSightActionInterface } from '../../interfaces/update-sight-actions.interface';

@Injectable()
export class SightsEffects {
  getSightsubscription: Subscription;
  constructor(private actions: Actions,
    private SightApiService: SightApiService,
    private store: Store<TemplarState>
  ) { }

  @Effect()
  loadSightsData$: any = this.actions.pipe(ofType(SightActions.Actions.LoadSightsData))
    .pipe(switchMap((action: Action) => {
      this.SightApiService.getSights().subscribe((Sight: SightInterface[]) => {
        Sight.sort((a, b) => {
          return a.position < b.position ? -1 : 1;
        })
        this.store.dispatch(new SightActions.SetSightsDataAction(Sight));
      });
      return [];
    }));

  @Effect()
  deleteSightData$: any = this.actions.pipe(ofType(SightActions.Actions.DeleteSightData))
    .pipe(switchMap((action: Action) => {
      const id: string = action['payload'];
      this.SightApiService.deleteSight(id).subscribe();
      return [];
    }));

  @Effect()
  addSightData$: any = this.actions.pipe(ofType(SightActions.Actions.AddSightData))
    .pipe(switchMap((action: Action) => {
      const SightData: SightInterface = action['payload'];
      this.SightApiService.addSight(SightData).subscribe();
      return [];
    }));

  @Effect()
  loadSightInfoData$: any = this.actions.pipe(ofType(SightActions.Actions.LoadActiveSightData))
    .pipe(switchMap((action: Action) => {
      const id: string = action['payload'];
      if (id) {
        this.getSightsubscription = this.SightApiService.getSight(id).subscribe((Sight: SightInterface) => {
          this.store.dispatch(new SightActions.SetActiveSightDataAction(Sight));
        });
      } else {
        this.store.dispatch(new SightActions.SetActiveSightDataAction(null));
        if (this.getSightsubscription) {
          this.getSightsubscription.unsubscribe();
          this.getSightsubscription = null;
        }
      }
      return [];
    }));

  @Effect()
  updateSightSightData$: any = this.actions.pipe(ofType(SightActions.Actions.UpdateActiveSightData))
    .pipe(switchMap((action: Action) => {
      const payload: UpdateSightActionInterface = action['payload'];
      this.SightApiService.updateSight(payload.sightData, payload.id).subscribe();
      return [];
    }));
}
