import { AudiosStateInterface } from '../../interfaces';
import { AudiosActions } from './audios.actions';
import { AudioInterface } from 'src/modules/dashboard/interfaces';

const initialState: AudiosStateInterface = {
  audios: []
};

export function audiosReducer(state: AudiosStateInterface = initialState, action: AudiosActions.AudiosActionsType): AudiosStateInterface {
  switch (action.type) {
    case AudiosActions.Actions.SetAudiosData: {
      return { ...state, audios: <AudioInterface[]>action["payload"] };
    }
    default: {
      return state;
    }
  }
}
