import { Injectable } from "@angular/core";
import { Resolve, ActivatedRouteSnapshot } from '@angular/router';
import { Store } from '@ngrx/store';
import { TemplarState } from '../state-management';
import { Observable } from 'rxjs';
import { ofType, Actions } from '@ngrx/effects';
import { take } from 'rxjs/operators';
import { AccesscodesActions } from '../state-management/states/accesscodes';

@Injectable()
export class AccesscodInfoResolver implements Resolve<void> {
  constructor(private store: Store<TemplarState>, private actions: Actions) { }

  resolve(route: ActivatedRouteSnapshot): Observable<void> {
    this.store.dispatch(new AccesscodesActions.LoadActiveAccesscodesDataAction(route.params.id));
    return this.actions.pipe(ofType(AccesscodesActions.Actions.SetActiveAccesscodData), take(1));
  }
}