import { Component, OnInit, Input, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { Observable, combineLatest } from 'rxjs';
import { FormGroup } from '@angular/forms';
import { map, take } from 'rxjs/operators';
import { UploadService } from '../../services';
import { FileInterface } from '../../interfaces/file.interface';
import { Store, Action } from '@ngrx/store';
import { TemplarState, AudiosActions } from '../../state-management';
import { Actions, ofType } from '@ngrx/effects';

@Component({
  selector: 'app-data-table',
  templateUrl: './data-table.component.html'
})
export class DataTableComponent<T> {
  @Input() items$: Observable<T[]>;
  @Input() addItems$: Observable<T[]>;
  @Input() allowEdit: boolean;
  @Input() allowAdd: boolean;
  @Input() itemName: string;
  @Input() form: FormGroup;
  @Input() formFieldName: string;
  @Input() customNameField: string;
  @Input() customDescField: string;
  @Input() uploadFolder: string;

  @Output() addButtonClick: EventEmitter<void> = new EventEmitter<void>();
  @Output() addItem: EventEmitter<string> = new EventEmitter<string>();
  @Output() editItem: EventEmitter<T> = new EventEmitter<T>();
  @Output() deleteItem: EventEmitter<T> = new EventEmitter<T>();

  @ViewChild('audioUpload') audioUpload: ElementRef<HTMLInputElement>;

  isAdd: boolean = false;
  itemToAdd: string = '';
  uploadingAudio: boolean;

  get filteredAddItems$(): Observable<T[]> {
    if (!this.addItems$ || !this.items$) {
      return this.addItems$;
    }
    return combineLatest(this.addItems$, this.items$).pipe(map((data: [T[], T[]]) => {
      return data[0] && data[1] ?
        data[0].filter((item: T) => data[1].findIndex((value: T) => value['id'] === item['id']) === -1) :
        data[0];
    }))
  }

  get itemNameLowerCase(): string {
    return this.itemName ? this.itemName.toLowerCase() : this.itemName;
  }

  get isAudioUploader(): boolean {
    return this.itemName && this.itemName.toLowerCase() === 'audio';
  }

  constructor(private actions: Actions, private uploadService: UploadService, private store: Store<TemplarState>) {
  }

  onAddButtonClick(): void {
    this.addButtonClick.emit();
    if (this.isAudioUploader) {
      this.audioUpload.nativeElement.click();
    } else {
      this.isAdd = true;
    }
  }

  onAddItem(): void {
    this.addItemFunc(this.itemToAdd);
    this.itemToAdd = '';
  }

  onEditItem(item: T): void {
    this.editItem.emit(item);
  }

  onDeleteItem(item: T): void {
    if (this.form && this.formFieldName) {
      let newList: string[] = this.form.value[this.formFieldName] || [];
      newList = newList.filter((id: string) => item['id'] !== id);
      this.form.get(this.formFieldName).setValue(newList);
    }
    this.deleteItem.emit(item);
    if (this.isAudioUploader) {
      this.store.dispatch(new AudiosActions.DeleteAudioDataAction(item['id']));
    }
  }

  getName(item: T): string {
    return this.customNameField ? item[this.customNameField] : item['name']['_en'];
  }

  getDescription(item: T): string {
    return this.customDescField ? item[this.customDescField] : item['desc']['_en'];
  }

  startUploadAudio(event: FileList): void {
    this.uploadingAudio = true;
    this.uploadService.uploadAudio(event, this.uploadFolder).subscribe((file: FileInterface) => {
      this.store.dispatch(new AudiosActions.AddAudioDataAction({ audio_content: file.url, audio_name: file.fileName }));
      this.uploadingAudio = false;
      this.actions.pipe(ofType(AudiosActions.Actions.AudioDataAdded)).pipe(take(1)).subscribe((action: Action) => {
        const id: string = action['payload'];
        this.addItemFunc(id);
      });
    });
  }

  private addItemFunc(id: string): void {
    if (this.form && this.formFieldName) {
      let newList: string[] = this.form.value[this.formFieldName] || [];
      newList.push(id);
      this.form.get(this.formFieldName).setValue(newList);
    }
    this.addItem.emit(id);
    this.isAdd = false;
  }
}
