import { SightStateInterface } from '../../interfaces';
import { SightInterface } from 'src/modules/dashboard/interfaces';
import { SightActions } from './sight.actions';


const initialState: SightStateInterface = {
  sights: [],
  activeSight: null
};

export function sightReducer(state: SightStateInterface = initialState, action: SightActions.SightsActionsType): SightStateInterface {
  switch (action.type) {
    case SightActions.Actions.SetSightsData: {
      return { ...state, sights: <SightInterface[]>action["payload"] };
    }
    case SightActions.Actions.SetActiveSightData: {
      return { ...state, activeSight: <SightInterface>action["payload"] };
    }
    default: {
      return state;
    }
  }
}
