import { Action } from '@ngrx/store';
import { AccesscodesInterface } from 'src/modules/dashboard/interfaces';
import { UpdateAccesscodesActionInterface } from '../../interfaces/update-accesscodes-actions.interface';

export namespace AccesscodesActions {

  export enum Actions {
    LoadAccesscodesData = '[Accesscodes] LoadAccesscodesData',
    SetAccesscodesData = '[Accesscodes] SetAccesscodesData',
    SetActiveAccesscodData = '[Accesscodes] SetActiveAccesscodData',
    DeleteAccesscodesData = '[Accesscodes] DeleteAccesscodesData',
    AddAccesscodesData = '[Accesscodes] AddAccesscodesData',
    LoadActiveAccesscodesData = '[Accesscodes] LoadActiveAccesscodesData',
    UpdateActiveAccesscodesData = '[Accesscodes] UpdateActiveAccesscodesData'
  }

  export class LoadAccesscodesDataAction implements Action {
    readonly type: Actions = Actions.LoadAccesscodesData;
    constructor() { }
  }

  export class LoadActiveAccesscodesDataAction implements Action {
    readonly type: Actions = Actions.LoadActiveAccesscodesData;
    constructor(public payload: string) { }
  }

  export class SetAccesscodesDataAction implements Action {
    readonly type: Actions = Actions.SetAccesscodesData;
    constructor(public payload: AccesscodesInterface[]) { }
  }

  export class SetActiveAccesscodDataAction implements Action {
    readonly type: Actions = Actions.SetActiveAccesscodData;
    constructor(public payload: AccesscodesInterface) { }
  }

  export class UpdateActiveAccesscodesDataAction implements Action {
    readonly type: Actions = Actions.UpdateActiveAccesscodesData;
    constructor(public payload: UpdateAccesscodesActionInterface) { }
  }

  export class DeleteAccesscodesDataAction implements Action {
    readonly type: Actions = Actions.DeleteAccesscodesData;
    constructor(public payload: string) { }
  }

  export class AddAccesscodesDataAction implements Action {
    readonly type: Actions = Actions.AddAccesscodesData;
    constructor(public payload: AccesscodesInterface) { }
  }

  export type AccesscodesActionsType = LoadAccesscodesDataAction |
                                 SetAccesscodesDataAction |
                                 SetActiveAccesscodDataAction |
                                 DeleteAccesscodesDataAction |
                                 AddAccesscodesDataAction |
                                 LoadActiveAccesscodesDataAction |
                                 UpdateActiveAccesscodesDataAction;
}
