import { Component, OnInit, Renderer2 } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/filter';
import { AbstractComponent } from 'src/modules/shared';
import { TemplarState, citiesSelectors, routesSelectors, sightSelectors, AudiosActions, styleSelectors } from '../../state-management';
import { Store } from '@ngrx/store';
import { CityInterface, SightInterface, StyleInterface, SelectItemInterface } from '../../interfaces';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { SightActions } from '../../state-management/states/sight';
import { takeUntil, take, map } from 'rxjs/operators';
import { SightLength } from '../../enums';
import { MapsService, UploadService } from '../../services';
import { FileInterface } from '../../interfaces/file.interface';

@Component({
  selector: 'app-sight-info',
  templateUrl: './sight-info.component.html',
  styleUrls: ['./sight-info.component.scss']
})
export class SightInfoComponent extends AbstractComponent implements OnInit {
  editSight: boolean;
  updateSightForm: FormGroup;
  SightLength = SightLength;
  map: any;
  marker: any;
  uploadingBackground: boolean;
  uploadingAudio: boolean;
  dropdownList = [];
  selectedItems = [];
  dropdownSettings = {
    singleSelection: false,
    text: "Select styles",
    selectAllText: 'Select All',
    unSelectAllText: 'UnSelect All',
    enableSearchFilter: true,
    classes: ""
  };

  sight$: Observable<SightInterface> = this.store.select(sightSelectors.getActiveSightSelector).pipe(takeUntil(this.destroyed$));
  cities$: Observable<CityInterface[]> = this.store.select(citiesSelectors.getCitiesSelector).pipe(takeUntil(this.destroyed$));
  styles$: Observable<StyleInterface[]> = this.store.select(styleSelectors.getStylesSelector).pipe(takeUntil(this.destroyed$))

  get sightLengths(): SightLength[] {
    return Object.keys(SightLength).map((key: string) => SightLength[key]);
  }

  get currentStyles$(): Observable<StyleInterface[]> {
    return this.styles$.pipe(map((styles: StyleInterface[]) => {
      return styles ?
        styles.filter((style: StyleInterface) => {
          return this.updateSightForm.value && this.updateSightForm.value.style && this.updateSightForm.value.style.indexOf(style.id) !== -1;
        }) : [];
    }));
  }

  constructor(
    private store: Store<TemplarState>,
    private router: Router,
    private formBuilder: FormBuilder,
    private activateRoute: ActivatedRoute,
    private mapsService: MapsService,
    private uploadService: UploadService,
    private renderer: Renderer2
  ) {
    super();

    this.updateSightForm = this.formBuilder.group({
      name: this.formBuilder.group({
        _en: ['', [
          Validators.required,
          Validators.minLength(3)
        ]],
        _es: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _pt: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _de: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _fr: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _it: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]]
      }),
      desc: this.formBuilder.group({
        _en: ['', [
          Validators.required,
          Validators.minLength(3)
        ]],
        _es: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _pt: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _de: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _fr: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]],
        _it: ['', [
          //Validators.required,
          Validators.minLength(3)
        ]]
      }),
      audi_url: ['', [
        Validators.required,
        Validators.minLength(3)
      ]],
      background_url: ['', [
        Validators.required,
        Validators.minLength(3)
      ]],
      position: ['', [
        //Validators.required
      ]],
      city_id: ['', [
        Validators.required
      ]],
      lenght: ['', [
        Validators.required
      ]],
      location_lat: ['', [
        Validators.required
      ]],
      location_lng: ['', [
        Validators.required
      ]],
      price: ['', [
        Validators.required,
        Validators.minLength(3)
      ]],
      style: []
    })

    this.sight$.subscribe((sight: SightInterface) => {
      sight ? this.updateSightForm.patchValue(sight) : this.updateSightForm.reset();
    });

    this.updateSightForm.valueChanges.pipe(takeUntil(this.destroyed$)).subscribe((value: SightInterface) => {
      if (this.marker) {
        if (value.location_lat !== this.marker._latlng.lat || value.location_lng !== this.marker._latlng.lng) {
          this.marker = this.mapsService.setMarker(this.map, this.marker, value.location_lat, value.location_lng);
        }
      }
    });
  }

  ngOnInit() {
    this.map = this.mapsService.createMap(this.updateSightForm.get('location_lat').value, this.updateSightForm.get('location_lng').value);
    this.map.on('click', (e: any) => {
      this.marker = this.mapsService.setMarker(this.map, this.marker, e.latlng.lat, e.latlng.lng);
      this.updateSightForm.get('location_lat').setValue(e.latlng.lat, { emitEvent: false });
      this.updateSightForm.get('location_lng').setValue(e.latlng.lng, { emitEvent: false });
    });

    this.sight$.pipe().subscribe((sight: SightInterface) => {
      this.editSight = !!sight;
      if (sight) {
        this.marker = this.mapsService.setMarker(this.map, this.marker, sight.location_lat, sight.location_lng);
      }
    });

    this.styles$.subscribe((data: StyleInterface[]) => {
      this.dropdownList = this.getSelectItems(data);
    });

    this.currentStyles$.pipe(take(1)).subscribe((styles: StyleInterface[]) => {
      this.selectedItems = this.getSelectItems(styles);
    });
  }

  ngOnDestroy() {
    this.store.dispatch(new SightActions.LoadActiveSightDataAction(null));
  }

  saveSight() {
    this.updateSightForm.value.position === null || '' || 0 ? this.updateSightForm.get('position').setValue(500) : null;

    if (this.editSight) {
      this.sight$.pipe(take(1)).subscribe((sight: SightInterface) => {
        this.store.dispatch(new SightActions.UpdateActiveSightDataAction({ sightData: { ...sight, ...this.updateSightForm.value }, id: this.activateRoute.snapshot.paramMap.get('id') }));
      });
    } else {
      this.store.dispatch(new SightActions.AddSightDataAction(this.updateSightForm.value));
    }
    this.goBack();
  }

  goBack(): void {
    this.router.navigate(['/sight']);
  }

  getLengthName(value: SightLength): string {
    return Object.keys(SightLength).find((key: string) => SightLength[key] === value);
  }

  startUploadBackground(event: FileList) {
    this.uploadingBackground = true;
    this.uploadService.uploadImage(event, 'sights/images').subscribe((file: FileInterface) => {
      this.updateSightForm.get('background_url').setValue(file.url);
      this.uploadingBackground = false;
    });
  }

  startUploadAudio(event: FileList) {
    this.uploadingAudio = true;
    this.uploadService.uploadAudio(event, 'sights/audio').subscribe((file: FileInterface) => {
      this.updateSightForm.get('audi_url').setValue(file.url);
      this.store.dispatch(new AudiosActions.AddAudioDataAction({ audio_content: file.url, audio_name: file.fileName }));
      this.uploadingAudio = false;
    });
  }

  // styles functions

  onItemSelect(item: SelectItemInterface): void {
    const style = this.updateSightForm.value.style || [];
    style.push(item.id);
    this.updateSightForm.get('style').setValue(style);
  }

  OnItemDeSelect(item: SelectItemInterface): void {
    let style = this.updateSightForm.value.style || [];
    style = style.filter(id => id !== item.id);
    this.updateSightForm.get('style').setValue(style);
  }

  onSelectAll(items: SelectItemInterface[]): void {
    this.updateSightForm.get('style').setValue(items.map((item: SelectItemInterface) => item.id));
  }

  onDeSelectAll(items: SelectItemInterface[]): void {
    this.updateSightForm.get('style').setValue([]);
  }

  private getSelectItems(data: StyleInterface[]): SelectItemInterface[] {
    return data.map((style: StyleInterface) => {
      return { itemName: style.name._en, id: style.id } as SelectItemInterface;
    });
  }
  onAddressFocus(event: FocusEvent) {
    this.renderer.addClass((event.target as HTMLElement).parentElement.parentElement, 'input-group-focus');
  }

  onAddressBlur(event: FocusEvent) {
    this.renderer.removeClass((event.target as HTMLElement).parentElement.parentElement, 'input-group-focus');
  }
}
